	module.exports = {"pathContext":{"group":[{"node":{"id":"68979717-e76f-5c25-add1-e35b2d9d38ac","slug":"bmj-group-at-coasp-2010","title":"BMJ Group at COASP 2010","excerpt":"<p>The BMJ Group was represented at the 2nd Conference for Open Access Scholarly Publishers (COASP) by Dr Trish Groves of the BMJ. You can watch her presentation on open access at the BMJ Group here. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2010/09/29/bmj-group-at-coasp-2010/\">Read More&#8230;</a></p>\n","status":"publish","date":"September 29, 2010","template":"","format":"standard"}},{"node":{"id":"df301e64-0f8e-5f76-9e95-5cabe7c87cf9","slug":"have-your-research-seen-by-the-world","title":"Have your research seen by the world!","excerpt":"<p>  BMJ Open is now welcoming submissions. Submit now – and your work could be included in the launch of our brand new open access journal. Instructions for authors are available here and you can read more about BMJ Open&#8216;s aims and scope here. Open access has long been part of the BMJ Group ethos – BMJ research articles have been [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2010/09/20/have-your-research-seen-by-the-world/\">Read More&#8230;</a></p>\n","status":"publish","date":"September 20, 2010","template":"","format":"standard"}},{"node":{"id":"0577cf4c-f687-5dfd-95ef-255ac38170c6","slug":"bmj-open-instructions-for-authors-posted","title":"BMJ Open instructions for authors posted","excerpt":"<p>Instructions on how to prepare a manuscript for submission to BMJ Open can be found here. Our manuscript submission site is in development; a notice will be posted on this site as soon as it is ready, so keep checking back. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2010/08/31/bmj-open-instructions-for-authors-posted/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 31, 2010","template":"","format":"standard"}},{"node":{"id":"ddcdddd5-09f7-5739-95b4-fa85ffb99982","slug":"press-release","title":"Press release","excerpt":"<p>The official press release announcing BMJ Open can be found here. It includes a comment from Dr Fiona Godlee, BMJ Editor-in-Chief, that helps set BMJ Open in the context of the BMJ Group&#8217;s history of open access, and its mission to help improve patient outcomes worldwide. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2010/08/23/press-release/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 23, 2010","template":"","format":"standard"}},{"node":{"id":"ada907ea-1526-5c38-a703-66b37194f664","slug":"bmj-open-faqs-posted","title":"BMJ Open FAQs posted","excerpt":"<p>A page that provides more specific information on BMJ Open has been added to the site here. Topics covered include BMJ Open&#8216;s required reporting standards for submissions, data sharing policies, article processing charges, peer review and copyright agreement. We will be adding to this document but meanwhile you can click on the Contact us link with any [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2010/08/20/bmj-open-faqs-posted/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 20, 2010","template":"","format":"standard"}},{"node":{"id":"7b3bcf81-31b7-5de8-8800-29e2387d2007","slug":"wider-open-access-developments-at-the-bmj-group","title":"Wider open access developments at the BMJ Group","excerpt":"<p>By adopting an open access policy for research articles, BMJ Open follows in the footsteps of the BMJ itself. All research articles published in the BMJ are available open access, a policy that dates from 1998. A recent editorial in the BMJ talks about the launch of BMJ Open and the BMJ&#8216;s new policy on [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2010/08/19/wider-open-access-developments-at-the-bmj-group/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 19, 2010","template":"","format":"standard"}},{"node":{"id":"73027541-7763-5d83-b88a-6db88158f828","slug":"bmj-open-accessible-medical-research","title":"BMJ Open: accessible medical research","excerpt":"<p>Welcome to the BMJ Open blog, where we will be documenting our countdown to launch later this year. BMJ Open is an open access journal for general medical research. Using a continuous publication model the journal will provide rapid publication for research from any medical discipline or therapeutic area. Not only will the journal publish [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2010/08/16/bmj-open-accessible-medical-research/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 16, 2010","template":"","format":"standard"}}],"pathPrefix":"posts","first":false,"last":true,"index":26,"pageCount":26,"additionalContext":{}}}

